.gallery {
  &__list {
    margin-bottom: 32px;

    display: flex;
    flex-direction: column;
    align-items: center;

    gap: 32px;

    @include onDesktop {
      gap: 40px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__button {
    width: 100%;
    height: 56px;

    font-size: 16px;
    font-weight: 700;
    line-height: 14px;

    color: $color-blue;
    background-color: $color-white;

    border: 2px solid $color-blue;
    border-radius: 8px;

    @include onDesktop {
      width: 150px;
    }
  }
}
