.place {
  &__image-container {
    margin-bottom: 16px;

    display: flex;
    gap: 20px;

    @include onDesktop {
      gap: 24px;
    }
  }

  &__image {
    width: 131px;
    height: 131px;

    object-fit: cover;
    object-position: center;

    border-radius: 8px;

    @include onDesktop {
      width: 100%;
      height: 100%;
    }
  }

  &__info {
    font-size: 16px;
    line-height: 27px;
  }

  &__address {
    font-weight: 700;

    color: $color-blue;
  }
}
